* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

html,
body {
  font-family: 'Montserrat', sans-serif;
}

a {
  text-decoration: none;


}

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-left: 80px;
 
 
 
}

.navegation {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;

  padding-top: 2.5%;
  padding-bottom: 2.5%;


  margin-left: 6%;
  margin-right: 6%;
}

.navegation__text {
  padding-top: 1.8%;
  margin-left: 20%;
  margin-right: 10%;

  width: 100%;


}

.navegation__pag {
  color: #168282;
  font-weight: 500;
  font-size: medium;
  padding-left: 5%;
  padding-right: 5%;


}

.navegation__here {
  font-weight: 700;
  color: #168282;
  padding-left: 5%;
  padding-right: 5%;
}

.navegation__block {

  width: 100%;
  z-index: -1;
  background: #ffffff;
  box-shadow: -11px 4px 9px rgba(0, 0, 0, 0.15);
}

.navegation__icon {

  width: 20%;
}

.title{
  margin-top: 6%;
  margin-left: 6%;

}

.allproducts {
  margin-top: 4%;
  margin-left: 6%;
  margin-right: 10%;

  display: flex;
  flex-direction: row;

}

h2 {
  color: #137070;
}

.line {
  margin-top: 1%;
  margin-bottom: 3%;
  width: 90%;
  background-color: #137070;
  border: 0.5px solid #137070;
  border-radius: 2px;

}

.product__info {
  width: 271px;
  height: 363px;
  
  margin-right: 5%;

  border: 1px solid #007A7A;
  display: flex;
  flex-direction: column;
  align-items: center;
  
  border-radius: 25px;

  padding-top: 5%;

  margin-bottom: 10%;
  

  
}

.product__name{
  padding-top: 5%;
  font-size: larger;
  color: #646464;
  font-weight: 600;
}

.material{
  color: #646464;
  font-weight: 400;
  font-size: medium;
}

.price{
  color: #646464;
  font-weight: 600;
  font-size: larger;

  margin-top: 3%;
  

}

.filters{
  display: flex;
  flex-direction: column;
  width: 280px;
  
}

.filters_text{
  color: #646464;
  font-size: larger ;
}

.filtes__material{
  margin-top: 15%;
}

.filters__price{
  margin-top: 15%;
}

input {
  border: none;
  text-decoration: none;
}

.form {
  width: 270px;
  height: 49px;
  margin-top: 0.7%;
  padding-left: 2%;
  border: 1px solid #137070;
  border-radius: 14px;
  color: #414040;
}

.footer {
  background-color: #137070;
  padding-left: 6%;
  padding-top: 3%;
  padding-bottom: 3%;
  margin-top: 3%;
}

.footer__contain {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.footer__text {
  padding-left: 30%;
  color: #ffffff;
  padding-top: 1%;
  font-size: small;
}



.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}

.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: rgb(0, 122, 122);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}

.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}





@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(24px, 0);
  }
}